*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    background: #17181a;
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
    position: relative;
}
main{
    position: absolute;
}

#logo{
    color: white;
    font-family: "Abril Fatface";
    position: relative;
    z-index: 2;
}

.nav-header{
    min-height: 10vh;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: white;
    width: 80%;
    margin: auto;
    padding: 2rem 0.5rem;
    z-index: 9999;

}
.line1,.line2{
    width: 3rem;
    height: 0.2rem;
    margin: 0.4rem;
    background: white;
    pointer-events: none;
}

.slide{
    min-height: 100vh;
    margin: 0 auto 0 auto;
    width: 80%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;
    background: #17181a;
}
.hero-desc{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex: 1;
    flex-basis: 25rem;
    padding-left: 5rem;
    position: relative;
}

.hero-img{
    position: relative;
    flex: 1;
    flex-basis: 21rem;
    overflow: hidden;

}
.hero-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.fashion-span{
    color: rgb(255, 139, 93);
}
.mountain-span{
    color: rgb(86, 124, 228);
}
.hike-span{
    color: rgb(167, 110, 231);
}

.hero-desc h2 {
    font-family: "Abril Fatface";
    font-size: 2rem;
}
.hero-desc p {
    padding: 3rem 0;
    font-size: 1rem;
    color: rgb(223, 223, 223);
}
.explore {
    padding:  1rem 2rem;
    background: none;
    border: none;
    color: white;
    border: 1px solid white;
    font-size: 1rem;
    text-decoration: none;
    align-self: flex-start;
    cursor: pointer;
}

.nav-bar{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 1;
    opacity: 1;
    clip-path: circle(25px at 100% -10%);
    -webkit-clip-path: circle(25px at 100% -10%);
}
.burger{
    cursor: pointer;
    z-index: 999;

}
.nav-links{
    font-size: 2rem;
    flex-basis: 30rem;
    z-index: -1111;
}
.nav-links h3{
    padding: 1rem 1rem;
}

.contact{
    flex-basis: 30rem;
    z-index: -11111;
}
.contact h2{
    font-size: 2.5rem;
    padding: 2rem 0;
}

.contact p{
    font-size: 1rem;
    line-height: 1.5rem;
}
.reveal-img,
.reveal-text{
    position: absolute;
    width: 100%;
    height: 150%;
    background: #17181a;
    left: 0;
    top: -10%;
}

.cursor{
    width: 3rem;
    height: 3rem;
    border: 2px solid white;
    border-radius: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    pointer-events: none;
    transition: all 0.5s ease-in-out;
    transition-property: background, transform;
    transform-origin: 90% 75%;
}


.cursor.nav-active{
    background: #fff;
    transform: scale(2);
    opacity: 0.7;
}
.cursor.explore-active{
    background: rgb(86, 128, 223);
    transform: scale(2);
    opacity: 0.7;
}
.cursor-text {
    position: relative;
    top: 5%;
    left: 33%;
    font-size: 0.5rem;
    color: #fff;
}
.title{
    position: relative;
    overflow: hidden;
}
.title-swipe{
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    background-color: lightblue;
    mix-blend-mode: darken;
    transform: translateY(100%);

}
.t-swipe1 {
    background: rgb(86, 124, 228);
}
.t-swipe2 {
    background: rgb(167, 110, 231);
}
.t-swipe3 {
    background: rgb(225, 139, 93);
}


.scrollmagic-pin-spacer{
    position: static !important;
}

.hide{
    overflow: hidden;
}

/*  Other pages */
.fashion1,.fashion2,.fashion3{
    min-height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 70%;
    margin: auto;
    color: #fff;
    margin-top: 5rem;
    position: relative;
}
.fashion-img{
    margin-bottom: 10em;
    height: 80vh;
    flex-basis: 30rem;
}
.fashion-text{
    position: relative;
    flex-basis: 25rem;
}
.fashion-img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.fashion-text h1{
    font-size: 4rem;

}
.fashion1 h1{
    color: rgb(255, 139, 93);
}
.fashion2 h1{
    color: rgb(167, 110, 231);
}
.fashion3 h1 {
    color: rgb(86, 128, 223);
}

.fashion-text p {
    font-size: 1rem;
    padding: 2rem 1.5rem 0 0;
    margin-bottom: 10rem;
}
.fashion-nr{
    opacity: 0.2;
}

.swipe{
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: translateX(-100%);
}
.swipe1{
    background: rgb(167, 110, 231);
    z-index: 5;
}
.swipe2{
    background: rgb(86, 128, 223);
    z-index: 4;
}
.swipe3{
    background: rgb(255, 139, 93);
    z-index: 3;
}


@media screen and (max-width: 1024px) {
    main{
        width:100%;
        margin: 0;

    }
    .slide{
        display: block;
        width:100%;
        text-align: center;
        margin-top: 4rem;
    }
    .hero-img{
        width: 80%;
        margin: auto;
    }
    .hero-desc{
        padding: 0;

    }
    .hero-desc h2{
        font-size: 1.6rem;
        padding: 1rem 0;
    }
    .hero-desc p{
        padding: 2rem 1rem;
        font-size: 1rem;
        line-height: 1.5rem;
        text-align: center;
    }
    .explore{
        width: 60%;
        margin: auto;
    }
    .detail-slide{
        width: 90%;
        text-align: center;
        display: block;
    }
    .fashion-img{
        width: 90%;
        margin: auto;
    }
    .fashion-text p{
        margin-bottom: 5rem;
        padding: 0;

    }
    .fashion-text h1{
        margin: 2rem 0;
    }
    .nav-bar{
        display: block;
    }
    .nav-links{
        width: 100vw;
        margin-top: 8rem;
        font-size: 2rem;
        text-align: center;
    }
    .contact{
        width: 100vw;
        margin: 0;
    }
    .contact h2{
        width: 100vw;
        font-size: 3rem;
        text-align: center;

    }
    .contact p{
        width: 100vw;
        font-size: 1rem;
        text-align: center;
    }
}

.undone{
    cursor: not-allowed;
}